.main-nav {
  position: relative;
  width: auto;
  padding: 10px 0 10px;
  background-color: $color-primary;
}

.main-nav__toggle {
  position: relative;
  left: 32px;
  display: block;
  width: 24px;
  height: 27px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: $color-white;
  box-shadow: 0 5px 0 0 $color-white, 0 10px 0 0 $color-white;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav.main-nav--opened.main-nav--closed {
  padding-bottom: 0px;
}

.main-nav--opened .main-nav__toggle {
  top: 2px;
  right: 28px;
  outline: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 12px;
    left: 2px;
    width: 20px;
    height: 2px;
    background-color: $color-white;
  }

  &::before {
    transform: rotate(43deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-43deg);
  }
}

.main-nav--closed .menu__item,
.user-menu--closed .user-menu__item {
  display: none;
}

.main-nav--opened .menu__item,
.user-menu--opened .user-menu__item {
  display: block;
}
